    margin-top: -35px; }
  &.new {
    color: #0c7254;
    &:before {
      content: '';
      position: absolute;
      width: 136px;
      height: 136px;
      border-radius: 100%;
      background-color: #1ee2a8;
      z-index: -1;
      top: 0;
      left: 0; }
    &:after {
      content: '';
      position: absolute;
      width: 20px;
      height: 3px;
      background-color: #fff;
      left: 25px;
      top: 100px; } }
  &.recommend {
    color: #fff;
    &:before {
      content: '';
      position: absolute;
      width: 136px;
      height: 136px;
      border-radius: 100%;
      background-color: rgba(36, 11, 11, 0.6);
      z-index: -1;
      top: 0;
      left: 0; }
    &:after {
      content: '';
      position: absolute;
      width: 20px;
      height: 3px;
      background-color: #ff0000;
      left: 25px;
      top: 100px; } } }

.hits-slider__wrapper {
  position: relative;
