    color: #1ee2a8;
    &:after {
      content: '';
      position: absolute;
      background: url(../img/hits-arrows.png) no-repeat;
      background-position: 0 -14px;
      width: 39px;
      height: 14px;
      right: 0;
      top: 6px;
      transition: .2s linear; } }
  &.recommend {
    color: #215e06;
    &:after {
      content: '';
      position: absolute;
      background: url(../img/hits-arrows.png) no-repeat;
      background-position: 0 0;
      width: 39px;
      height: 14px;
      right: 0;
      top: 6px;
      transition: .2s linear; } }
  &:hover {
