.hits-slider {
  margin-top: 20px;
  .slick-list {
    padding-top: 30px; }
  .arrow-left {
    position: absolute;
    z-index: 3;
    cursor: pointer;
    border-radius: 100% 0 0 100% / 50% 0 0 50%;
    background-color: #19caf0;
    width: 43px;
    height: 82px;
    top: 165px;
    left: calc(50% - 22px);
    transform: translateX(-50%);
    &:before {
      content: '';
      position: absolute;
      border-right: 2px dashed #fff;
      width: 2px;
      height: 44px;
      right: 0;
      top: calc(50% - 22px);
      opacity: .8; }
    &:after {
      content: '';
      position: absolute;
      background: url(../img/hits-arrow-left.png) no-repeat;
      width: 12px;
      height: 22px;
      left: 14px;
      top: calc(50% - 11px); } }
  .arrow-right {
    position: absolute;
    z-index: 3;
    cursor: pointer;
    border-radius: 0 100% 100% 0 / 0 50% 50% 0;
    background-color: #19caf0;
    width: 41px;
    height: 82px;
    top: 165px;
    right: calc(50% - 40px);
    &:after {
      content: '';
      position: absolute;
      background: url(../img/hits-arrow-right.png) no-repeat;
      width: 12px;
      height: 22px;
      right: 14px;
      top: calc(50% - 11px); } } }

